/*
SUMMARY

#VIEW -> Style for view and its elements

#TITLE -> Style for title only

#PIN -> Style for pin

#POSITION -> Handle positions in viewport for js
*/

body {
  overflow: hidden; /* Hide scrollbars */
}

/*
#VIEW Section

Container styling;

View general styling { 
  (absolute)
  element general styling;
}

View Number {
  view--number__element--number styling;
}

!important: For Title styling refer to Title section
*/

.view__container {
  position: absolute;
  height: 500vh;
  width: 500vw;
  transition: transform 2s ease-in-out;
  background: url("/assets/img/textureContainer.jpg");
}

.view {
  position: absolute;
  width: 110vw;
  height: 110vh;

  .element {
