/*
#TITLE Section

Title general styling {
  title__view--number styling;
}

!important: for pin styling, refer to pin section
*/

.title {
  position: relative;
  font-family: 'Lacquer';
  font-size: 75px;
  color: #f33a3a;
  text-align: center;

  &.title__view--1 {
    font-size: 105px;
    font-family: "Staatliches", cursive;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    stroke-dasharray: 10000;
    stroke-dashoffset: 10000;
    animation: dash 20s ease-in-out forwards;
  }

  &.title__view--2 {
    position: relative;
    top: 200px;
    left: 180px;

    &:first-child {
      top: 30px;
      left: -600px;
    }
  }

  &.title__view--4 {
    top: 75px;
  }
}

@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

/******************** Title styling END *********************/

/*
#PIN Section

  Pin general styling {
    pin--type-size {
      inner pin;
    }
  }
*/

.pin {
  &.pin--red-medium {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    background: #f33a3a;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    box-shadow: 4px 4px 8px rgba($color: #000000, $alpha: 0.3);

    .inner {
      background: #f33a3a;
      margin: auto;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      box-shadow: 4px 4px 8px rgba($color: #000000, $alpha: 0.3);
    }
  }
}



/** Positioning classes **/

.position--0 {
  transform: translate(-5vw, -5vh);
}

.position--1 {
  transform: translate(-105vw, -105vh);
}

.position--2 {
  transform: translate(-125vw, -215vh);
}

.position--3 {
  transform: translate(-85vw, -325vh);
}

.view--1 {
  left: 0vw;
  top: 0vh;
}

.view--2 {
  // min gap
  top: 110vh;
  left: 110vw;
}

.view--3 {
  top: 220vh;
  left: 130vw;
}

.view--4 {
  top: 330vh;
  left: 90vw;
}